import NewsApiService from './js-components/news-service' //          API
import LoadMoreBtn from './js-components/load-more-btn' //    ()    Load more
import Notiflix from 'notiflix'
import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

const searchForm = document.querySelector('#search-form');     //    
const input = document.querySelector('input[name="searchQuery"]');     //    
const hitsContainer = document.querySelector('.gallery');  //     
// const loadMoreBtn = document.querySelector('.load-more');  //     
let gallerySimpleLightbox = new SimpleLightbox('.gallery a'); //    

const loadMoreBtn = new LoadMoreBtn({  //        /  Load more,         LoadMoreBtn
    selector: '.load-more',  //     Load more
    hidden: true,  //     Load more 
}); 

const newsApiService = new NewsApiService(); //   '      API


searchForm.addEventListener('submit', onSearch)  //    
loadMoreBtn.refs.button.addEventListener('click', onLoadMore)  //    

let searchQuery = ''; //           fetchArticles

function onSearch(e) {  //     
    e.preventDefault();   //       
    newsApiService.query = input.value; //      
    loadMoreBtn.disable(); //     Loading... (    )
    loadMoreBtn.show(); //    (    )
    newsApiService.resetPage(); //   submit     1-    (   NewsApiService)
    console.log(newsApiService.query);
    newsApiService.fetchHits(searchQuery) //       API
        .then(hits => {
            if (hits.length === 0) {
                Notiflix.Notify.failure("Sorry, there are no images matching your search query. Please try again.");
                loadMoreBtn.hide(); //    (    )
            } else {
                clearHitsContainer(); //       
                appendHitsMarkup(hits); //       ' 
                Notiflix.Notify.success(`Hooray! We found ${hits.totalHits} images.`);
                loadMoreBtn.enable(); //     Load more (    )
                gallerySimpleLightbox.refresh(); //     
            }
            });    
}

function onLoadMore(e) { //     
    loadMoreBtn.disable(); //     Loading... (    )
    loadMoreBtn.show(); //    (    )
    newsApiService.fetchHits(searchQuery)  //       API
        .then(hits => {
            if (hits.length === 0) {
                Notiflix.Notify.failure("We're sorry, but you've reached the end of search results.");
                loadMoreBtn.hide(); //    (    )
            } else {
                appendHitsMarkup(hits); //       ' 
                Notiflix.Notify.success(`Hooray! We found ${hits.totalHits} images.`);
                loadMoreBtn.enable(); //     Load more (    )
                gallerySimpleLightbox.refresh(); //     
            }
        });
}

