function appendHitsMarkup(hits) { //          ' 
    hitsContainer.insertAdjacentHTML('beforeend', hitsTpl(hits)); 
}

function clearHitsContainer() { //          
    hitsContainer.innerHTML = '';
}

function hitsTpl(hits) { //       
    const markup = hits.map(hit => { //    API   html  ,       simplelightbox
        return `<div class="photo-card"> 
                <a href="${hit.largeImageURL}"><img class="img-card" src="${hit.webformatURL}" alt="${hit.tags}" title="${hit.tags}" loading="lazy"/></a> 
                <div class="info">
                  <p class="info-item">
                  <b>Likes</b> ${hit.likes}
                  </p>
                  <p class="info-item">
                  <b>Views</b> ${hit.views}
                  </p>
                  <p class="info-item">
                  <b>Comments</b> ${hit.comments}
                  </p>
                  <p class="info-item">
                  <b>Downloads</b> ${hit.downloads}
                  </p>
              </div>
            </div>`;
    })
    
        .join(''); // '        '' (   )
    return markup;
    // hitsContainer.innerHTML += markup; //   html   
}