export default class LoadMoreBtn {
    constructor({ selector, hidden = false }) {
        this.refs = this.getRefs(selector); //   this.refs     

        hidden && this.hide(); //        hidden=true     hide()
    }


getRefs(selector) {
    const refs = {};
    refs.button = document.querySelector(selector); //   
    refs.label = refs.button.querySelector('.label'); //   label
    refs.spinner = refs.button.querySelector('.spinner'); //   spin

    return refs;
    }
 

enable() { //         (     show/hide)
    this.refs.button.disabled = false; //   
    this.refs.label.textContent = 'Load more'; //     Load more
    this.refs.spinner.classList.add('is-hidden'); //    
    this.refs.button.classList.remove('is-hidden'); //  hidden,   
}

disable() { //       (     show/hide)
    this.refs.button.disabled = true; //   
    this.refs.label.textContent = 'Loading...'; //     Loading...
    this.refs.spinner.classList.remove('is-hidden'); //    
    this.refs.button.classList.add('is-hidden'); //  hidden,   
}

show() {  //    Load more 
    this.refs.button.classList.remove('is-hidden'); //  hidden,   
}

hide() { //    Load more 
    this.refs.button.classList.add('is-hidden'); //  hidden,   
    }
}