        incrementPage() { //           load more
            this.page += 1;
        }

        resetPage() { //       1-   
            this.page = 1;
        }

        get query() { //         
            return this.searchQuery;
        }
        set query(newQuery) { //         
            this.searchQuery = newQuery;
        }
